const GetScreen = (path) => {
    if (path === undefined) {
        return InnerScreensList[0]
    }
    const ind = InnerScreensList.findIndex(elem => elem.path === path)
    if (ind >= 0) {
        return InnerScreensList[ind]
    }
    return InnerScreensList[0]
}


